/**
 * @description Register the account to the api
 * @param account
 * @param MsgHandler return the error from the API
 * @returns
 */
export const FC_DeleteDocument = (
  data: {
    user_id: string;
    doc_id: string;
  },
  callback: Function
) => {
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();

      await axios.delete(`${API.AUTH}/userdetails`, {
        data: {
          doc_id: data.doc_id,
          user_id: data.user_id,
        },
      });

      dispatch<DeleteUserDocument>({
        type: ActionTypes.DELETE_USER_DOCUMENT,
        payload: data.doc_id,
      });

      callback(true, "");
    } catch (error) {
      callback(false, errorToText(error));
    }
  };
};

