import React, { Fragment } from "react";
import { connect } from "react-redux";
import { Auth, FC_AddUploadedDocumentToState } from "../../actions";
import { StoreState } from "../../reducers";
import {
  DocumentSubCategories,
  DocumentsCategories,
  DocumentUser,
} from "../../interfaces";
import { MdChevronRight } from "react-icons/md";
import SelectCategory from "./SelectCategory";
import SelectSubCategory from "./SelectSubCategory";
import Alert, { AlertInterface } from "../../components/Alert/Alert";
// axios for upload
import { setAxiosToken } from "../../utils/AxiosToken";
import axios from "axios";
import { API } from "../../utils/api";
import Modal, { ModalSize, Themes } from "../Modal/Modal";

// props for the component
interface AppProps {
  auth: Auth;
  category: DocumentsCategories | null;
  sub_category: DocumentSubCategories | null;
  active: boolean;
  close: Function;
  FC_AddUploadedDocumentToState: Function;
  UploadCompleted: Function | null;
}

interface AppState {
  loading: boolean;
  no_categories: boolean;
  category: DocumentsCategories | null;
  sub_category: DocumentSubCategories | null;
  sub_categories_list: DocumentSubCategories[];
  error: {
    target: string;
    msg: string;
  };
  success: boolean;
}

class _AddUserDocument extends React.Component<AppProps, AppState> {
  static defaultProps = {
    category: null,
    sub_category: null,
    UploadCompleted: null,
  };

  input_title: any;
  input_description: any;
  input_files: any;
  input_start_year: any;
  input_end_year: any;
  input_email: any;
  input_names: any;
  input_phone_number: any;
  input_company: any;
  input_year: any;
  input_school: any;

  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      success: false,
      error: {
        target: "",
        msg: "",
      },
      no_categories: false,
      category: this.props.category,
      sub_category: this.props.sub_category,
      sub_categories_list:
        this.props.category === null
          ? []
          : this.props.auth.document_types.sub_categories.filter(
              (itm) =>
                itm.category_id === this.props.category?.user_detail_category_id
            ),
    };

    // define inputs
    this.input_title = React.createRef();
    this.input_description = React.createRef();
    this.input_files = React.createRef();
    this.input_start_year = React.createRef();
    this.input_end_year = React.createRef();
    this.input_email = React.createRef();
    this.input_names = React.createRef();
    this.input_phone_number = React.createRef();
    this.input_company = React.createRef();
    this.input_year = React.createRef();
    this.input_school = React.createRef();
  }

  componentDidMount = () => {
    if (
      this.props.auth.document_types.categories.length <= 0 ||
      this.props.auth.document_types.sub_categories.length <= 0
    ) {
      this.setState({
        no_categories: true,
      });
    }
  };

  componentDidUpdate(prevProps: AppProps): void {
    if (
      (prevProps.category === null && this.props.category !== null) ||
      (prevProps.sub_category === null && this.props.sub_category !== null) ||
      prevProps.sub_category?.user_detail_subcategory_id !==
        this.props.sub_category?.user_detail_subcategory_id ||
      prevProps.category?.user_detail_category_id !==
        this.props.category?.user_detail_category_id
    ) {
      this.setState({
        category: this.props.category,
        sub_category: this.props.sub_category,
        sub_categories_list:
          this.props.category === null
            ? []
            : this.props.auth.document_types.sub_categories.filter(
                (itm) =>
                  itm.category_id ===
                  this.props.category?.user_detail_category_id
              ),
        error: {
          target: "",
          msg: "",
        },
      });
    }
  }

  selectCategoryHandler = (cat: DocumentsCategories | null) =>
    this.setState({
      category: cat,
      sub_category: null,
      sub_categories_list:
        cat === null
          ? []
          : this.props.auth.document_types.sub_categories.filter(
              (itm) => itm.category_id === cat?.user_detail_category_id
            ),
    });

  selectSubCategoryHandler = (subCat: DocumentSubCategories | null) =>
    this.setState({
      sub_category: subCat,
    });

  // upload data

  FC_UploadUserDocumentAPI = async (data: any, user_id: string) => {
    try {
      var formData = new FormData();
      console.log({ data, user_id: user_id });
      for (const key of Object.keys(data)) {
        formData.append(key, data[key]);
      }

      formData.append("user_id", user_id);
      formData.append("files", this.input_files.current.value);

      setAxiosToken();
      const res = await axios.post<{
        doc_id: string;
        message: string;
        files?: string;
      }>(`${API.AUTH}/userdetails`, formData, {
        onUploadProgress: (progressEvent: any) => {
          console.log({ progressEvent });
          console.log(progressEvent?.loaded, progressEvent.total);
        },
      });

      console.log({
        data: res.data,
      });

      this.setState({
        loading: false,
        success: true,
      });

      // set the clear document
      let new_document = {
        ...data,
        doc_id: res.data.doc_id,
        files: res.data?.files,
      };
      this.props.FC_AddUploadedDocumentToState(new_document);
      // call the complete handler
      if (this.props.UploadCompleted !== null) {
        this.props.UploadCompleted();
      }
      // timer for message
      setTimeout(() => {
        this.setState({
          success: false,
        });
      }, 3000);

      this.clearForm();
    } catch (error) {
      this.setState({
        loading: false,
        error: {
          target: "all",
          msg: "msg",
        },
      });
    }
  };

  clearForm = () => {
    // clear the form input
    if (this.input_title.current?.value !== undefined) {
      this.input_title.current.value = "";
    }
    if (this.input_description.current?.value !== undefined) {
      this.input_description.current.value = "";
    }
    if (this.input_files.current?.value !== undefined) {
      this.input_files.current.value = "";
    }
    if (this.input_start_year.current?.value !== undefined) {
      this.input_start_year.current.value = "";
    }
    if (this.input_end_year.current?.value !== undefined) {
      this.input_end_year.current.value = "";
    }
    if (this.input_email.current?.value !== undefined) {
      this.input_email.current.value = "";
    }
    if (this.input_names.current?.value !== undefined) {
      this.input_names.current.value = "";
    }
    if (this.input_phone_number.current?.value !== undefined) {
      this.input_phone_number.current.value = "";
    }
    if (this.input_company.current?.value !== undefined) {
      this.input_company.current.value = "";
    }
    if (this.input_year.current?.value !== undefined) {
      this.input_year.current.value = "";
    }
    if (this.input_school.current?.value !== undefined) {
      this.input_school.current.value = "";
    }
  };

  submitHandler = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    this.setState({
      error: {
        target: "",
        msg: "",
      },
    });
    let userDocument = {
      category_id: this.state.category?.user_detail_category_id,
      company: "",
      description: "",
      email: "",
      end_year: "",
      files: "",
      names: "",
      phone_number: "",
      school: { title: "", school_id: "" },
      start_year: "",
      sub_category_id:
        this.state.sub_category?.user_detail_subcategory_id !== undefined &&
        this.state.sub_category?.user_detail_subcategory_id.length >= 1
          ? this.state.sub_category?.user_detail_subcategory_id
          : "",
      title: "",
      year: "",
      approved: "Waiting",
      doc_id: "",
    } as DocumentUser;

    // title
    if (this.input_title.current?.value === undefined) {
      userDocument.title = "";
    } else {
      if (this.input_title.current?.value.length <= 0) {
        return this.setState({
          error: {
            target: "title",
            msg: "Please add the title",
          },
        });
      } else {
        userDocument.title = this.input_title.current?.value;
      }
    }

    // description
    if (this.input_description.current?.value === undefined) {
      userDocument.description = "";
    } else {
      if (this.input_description.current?.value.length <= 0) {
        return this.setState({
          error: {
            target: "description",
            msg: "Please add the description",
          },
        });
      } else {
        userDocument.description = this.input_description.current?.value;
      }
    }

    // school
    if (this.input_school.current?.value === undefined) {
      userDocument.school = { school_id: "", title: "" };
    } else {
      if (this.input_school.current?.value.length <= 0) {
        return this.setState({
          error: {
            target: "school",
            msg: "Please add the school",
          },
        });
      } else {
        userDocument.school = {
          school_id: "",
          title: this.input_school.current?.value,
        };
      }
    }

    // email
    if (this.input_email.current?.value === undefined) {
      userDocument.email = "";
    } else {
      if (this.input_email.current?.value.length <= 0) {
        return this.setState({
          error: {
            target: "email",
            msg: "Please add the email",
          },
        });
      } else {
        userDocument.email = this.input_email.current?.value;
      }
    }

    // names
    if (this.input_names.current?.value === undefined) {
      userDocument.names = "";
    } else {
      if (this.input_names.current?.value.length <= 0) {
        return this.setState({
          error: {
            target: "names",
            msg: "Please add the names",
          },
        });
      } else {
        userDocument.names = this.input_names.current?.value;
      }
    }

    // phone_number
    if (this.input_phone_number.current?.value === undefined) {
      userDocument.phone_number = "";
    } else {
      if (this.input_phone_number.current?.value.length <= 0) {
        return this.setState({
          error: {
            target: "phone_number",
            msg: "Please add the phone_number",
          },
        });
      } else {
        userDocument.phone_number = this.input_phone_number.current?.value;
      }
    }

    // company
    if (this.input_company.current?.value === undefined) {
      userDocument.company = "";
    } else {
      if (this.input_company.current?.value.length <= 0) {
        return this.setState({
          error: {
            target: "company",
            msg: "Please add the company",
          },
        });
      } else {
        userDocument.company = this.input_company.current?.value;
      }
    }

    // year
    if (this.input_year.current?.value === undefined) {
      userDocument.year = "";
    } else {
      if (this.input_year.current?.value.length <= 0) {
        return this.setState({
          error: {
            target: "year",
            msg: "Please add the year",
          },
        });
      } else {
        userDocument.year = this.input_year.current?.value;
      }
    }

    // start_year
    if (this.input_start_year.current?.value === undefined) {
      userDocument.start_year = "";
    } else {
      if (this.input_start_year.current?.value.length <= 0) {
        return this.setState({
          error: {
            target: "start_year",
            msg: "Please add the start_year",
          },
        });
      } else {
        userDocument.start_year = this.input_start_year.current?.value;
      }
    }

    // end_year
    if (this.input_end_year.current?.value === undefined) {
      userDocument.end_year = "";
    } else {
      if (this.input_end_year.current?.value.length <= 0) {
        return this.setState({
          error: {
            target: "end_year",
            msg: "Please add the end_year",
          },
        });
      } else {
        userDocument.end_year = this.input_end_year.current?.value;
      }
    }

    // files
    if (this.input_files.current?.value === undefined) {
      userDocument.files = "";
    } else {
      if (this.input_files.current?.value.length <= 0) {
        // return this.setState({
        //   error: {
        //     target: "files",
        //     msg: "Please add the files",
        //   },
        // });
        userDocument.files = "";
      } else {
        userDocument.files = this.input_files.current?.value;
      }
    }

    this.setState({
      loading: true,
    });

    // send the data
    this.FC_UploadUserDocumentAPI(userDocument, this.props.auth.user.user_id);
  };

  render() {
    let fields_access: string[] = [];
    if (this.state.category !== null) {
      fields_access = this.state.category?.fields.map((name) =>
        name.toLowerCase()
      );
    }

    return this.props.active === true ? (
      <Fragment>
        <Modal
          widthSizeClass={ModalSize.medium}
          backDrop={true}
          backDropClose={false}
          close={() => this.props.close()}
          displayClose={true}
          theme={Themes.default}
          title={
            <label className="text-xl m-0 text-bold text-gray-700">
              Add{" "}
              {this.state.category !== null &&
                `(${this.state.category?.title})`}
            </label>
          }
          padding={{ body: true, footer: true, title: true }}
        >
          <div className="px-2 py-0.5 bg-gray-100 text-xl rounded-md text-blue-900 font-bold">
            <button
              className="p-1 font-bold hover:underline"
              onClick={() => this.selectCategoryHandler(null)}
            >
              Select <MdChevronRight className="inline" />
            </button>
            {this.state.category !== null && (
              <button
                className="p-1 font-bold hover:underline"
                onClick={() => this.selectSubCategoryHandler(null)}
              >
                {this.state.category.title}{" "}
                {this.state.sub_categories_list.length >= 1 && (
                  <MdChevronRight className="inline" />
                )}
              </button>
            )}
            {this.state.sub_category !== null && (
              <button className="p-1 font-bold hover:underline">
                {this.state.sub_category.title}{" "}
              </button>
            )}
          </div>

          {this.state.category === null ? (
            <SelectCategory
              categories={this.props.auth.document_types.categories}
              selectCat={this.selectCategoryHandler}
            />
          ) : this.state.sub_categories_list.length >= 1 &&
            this.state.sub_category === null ? (
            <SelectSubCategory
              sub_categories={this.state.sub_categories_list}
              category={this.state.category}
              selectCat={this.selectSubCategoryHandler}
            />
          ) : (
            <div className="p-2">
              {this.state.error.target === "all" && (
                <Alert
                  theme={AlertInterface.danger}
                  title={"Failed to add details"}
                  description={this.state.error.msg}
                />
              )}

              {this.state.success === true && (
                <Alert
                  theme={AlertInterface.success}
                  title={"Details Uploaded successfuly"}
                />
              )}

              <form onSubmit={this.submitHandler}>
                <div className="bg-white">
                  {fields_access.includes("title") === true && (
                    <div className="p-2">
                      <label htmlFor="title" className="d-block">
                        Title
                      </label>
                      <input
                        ref={this.input_title}
                        type="text"
                        name="names"
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "title"
                            ? "border-red-500 border-2"
                            : "border-gray-500 border-2"
                        }`}
                        placeholder="Enter the title"
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "title" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>
                  )}

                  {fields_access.includes("description") === true && (
                    <div className="p-2">
                      <label htmlFor="description" className="d-block">
                        Description
                      </label>
                      <input
                        ref={this.input_description}
                        type="text"
                        name="names"
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "description"
                            ? "border-red-500 border-2"
                            : "border-gray-500 border-2"
                        }`}
                        placeholder="Enter the description"
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "description" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>
                  )}

                  {fields_access.includes("school") === true && (
                    <div className="p-2">
                      <label htmlFor="school" className="d-block">
                        School
                      </label>
                      <input
                        ref={this.input_school}
                        type="text"
                        name="names"
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "school"
                            ? "border-red-500 border-2"
                            : "border-gray-500 border-2"
                        }`}
                        placeholder="Your school"
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "school" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>
                  )}

                  {fields_access.includes("email") === true && (
                    <div className="p-2">
                      <label htmlFor="email" className="d-block">
                        Email
                      </label>
                      <input
                        ref={this.input_email}
                        type="text"
                        name="names"
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "email"
                            ? "border-red-500 border-2"
                            : "border-gray-500 border-2"
                        }`}
                        placeholder="Your email"
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "email" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>
                  )}

                  {fields_access.includes("names") === true && (
                    <div className="p-2">
                      <label htmlFor="names" className="d-block">
                        Names
                      </label>
                      <input
                        ref={this.input_names}
                        type="text"
                        name="names"
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "names"
                            ? "border-red-500 border-2"
                            : "border-gray-500 border-2"
                        }`}
                        placeholder="Your names"
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "names" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>
                  )}

                  {fields_access.includes("phone_number") === true && (
                    <div className="p-2">
                      <label htmlFor="phone_number" className="d-block">
                        Phone number
                      </label>
                      <input
                        ref={this.input_phone_number}
                        type="text"
                        name="phone_number"
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "phone_number"
                            ? "border-red-500 border-2"
                            : "border-gray-500 border-2"
                        }`}
                        placeholder="Your phone_number"
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "phone_number" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>
                  )}

                  {fields_access.includes("company") === true && (
                    <div className="p-2">
                      <label htmlFor="company" className="d-block">
                        Company
                      </label>
                      <input
                        ref={this.input_company}
                        type="text"
                        name="company"
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "company"
                            ? "border-red-500 border-2"
                            : "border-gray-500 border-2"
                        }`}
                        placeholder="Company name"
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "company" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>
                  )}

                  {fields_access.includes("year") === true && (
                    <div className="p-2">
                      <label htmlFor="year" className="d-block">
                        Year
                      </label>
                      <input
                        ref={this.input_year}
                        type="date"
                        name="year"
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "year"
                            ? "border-red-500 border-2"
                            : "border-gray-500 border-2"
                        }`}
                        placeholder="year"
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "year" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>
                  )}

                  {fields_access.includes("start_year") === true && (
                    <div className="p-2">
                      <label htmlFor="start_year" className="d-block">
                        Start year
                      </label>
                      <input
                        ref={this.input_start_year}
                        type="date"
                        name="start_year"
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "start_year"
                            ? "border-red-500 border-2"
                            : "border-gray-500 border-2"
                        }`}
                        placeholder="start_year"
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "start_year" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>
                  )}

                  {fields_access.includes("end_year") === true && (
                    <div className="p-2">
                      <label htmlFor="end_year" className="d-block">
                        End year
                      </label>
                      <input
                        ref={this.input_end_year}
                        type="date"
                        name="end_year"
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "end_year"
                            ? "border-red-500 border-2"
                            : "border-gray-500 border-2"
                        }`}
                        placeholder="end_year"
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "end_year" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>
                  )}

                  {fields_access.includes("files") === true && (
                    <div className="p-2">
                      <label htmlFor="files" className="d-block">
                        Files
                      </label>
                      <input
                        ref={this.input_files}
                        type="file"
                        name="files"
                        className={`input-white w-20 inline-block  ${
                          this.state.error?.target === "files"
                            ? "border-red-500 border-2"
                            : "border-gray-500 border-2"
                        }`}
                        placeholder="files"
                        disabled={this.state.loading}
                      />
                      {this.state.error?.target === "files" && (
                        <span className="text-red-600">
                          {this.state.error?.msg}
                        </span>
                      )}
                    </div>
                  )}

                  <div className="p-2  text-right">
                    <button
                      className="py-2 font-bold  px-8 rounded bg-hero text-white inline-block"
                      disabled={this.state.loading}
                      type="submit"
                    >
                      {this.state.loading === true ? (
                        <b className="animate__animated animate__fadeIn animate__infinite">
                          Saving data...
                        </b>
                      ) : (
                        "Save data"
                      )}
                    </button>
                  </div>
                </div>
              </form>
            </div>
          )}
        </Modal>
      </Fragment>
    ) : null;
  }
}

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const AddUserDocument = connect(mapStateToProps, {
  FC_AddUploadedDocumentToState,
})(_AddUserDocument);
